/*? Character Box ----------  */

.character-box {
  height: 100%;
  --i: max(1, var(--length));
  --box-width: calc(100% / var(--i) - 3px);

  width: var(--box-width);
  /* width: 240px; */

  --fill-perc: 0%;

  --c: var(--color);

  background-color: #ffffff12;

  background-image: linear-gradient(
    90deg,
    var(--c),
    var(--c) var(--fill-perc),
    #0000 var(--fill-perc),
    #0000
  );

  transform: skewX(-7deg);
  display: inline-flex;

  margin-right: 5px;

  transition: --fill-perc 0.2s, --c 0s 0.2s, width 0.2s, transform 0.2s;
  overflow: hidden;

  -webkit-user-drag: none;
}

.character-box:hover {
  transition: --fill-perc 0.2s, width 0.2s, transform 0.2s;
  --fill-perc: 100%;
}

.character-box.active {
  --fill-perc: 100%;

  --tx: calc((var(--box-width) - 20px) * -1);

  width: calc(100% + var(--box-width));
  transform: skewX(-7deg)
    translateX(
      calc(
        var(--tx) * var(--index) - var(--box-width) - var(--potrait-offset-x)
      )
    );
}

.character-box .portrait {
  height: 100%;
  margin-top: 30px;
  margin-left: var(--potrait-offset-x);

  transform: skewX(7deg) !important;
  --shadow-offset: 0px;

  animation: fly-in 0.2s linear 0s 1 normal forwards;
  transition: transform 0.2s, margin 0.2s, --shadow-offset 0.1s 0.15s;

  object-fit: cover;
  overflow: visible;

  z-index: -1;
}

.character-box:first-child {
  margin-left: -70px;
  padding-left: 70px;
}

.character-box:last-child {
  padding-right: 70px;
}

.character-box:hover .portrait {
  transform: skewX(7deg) translateY(-10px);
}

.character-box.active .portrait {
  margin-left: 200px;
  max-width: calc(50% + var(--potrait-offset-x) * 2);

  --shadow-offset: -80px;

  filter: drop-shadow(var(--shadow-offset) 0px 0px #0003)
    drop-shadow(var(--shadow-offset) 0px 0px #0006);
}

.character-box.active:last-child .portrait {
  margin-left: 220px;
}

/*? Character Box (INFO SECTION) ----------  */

.character-box .c-info {
  min-width: calc(50% - (var(--potrait-offset-x) * 2));
  height: 100%;

  --fill-amnt: 1px;

  background-image: linear-gradient(
    90deg,
    #000,
    #000 var(--fill-amnt),
    #0000 var(--fill-amnt),
    #0000
  );

  padding-left: 40px;

  transition: --fill-amnt 0.2s 0.1s;
}

.character-box.active .c-info {
  --fill-amnt: 100%;
  color: #000;
}

.c-info .memories {
  /* display: flex; */
  display: none;
  gap: 40px;

  align-items: center;
  padding-left: 10px;
}

.character-box.active .c-info .memories {
  display: flex;
}

.c-info .memories .img {
  --size: calc(var(--mwidth) * 0.15);
  height: var(--size);
  width: var(--size);

  display: inline-block;

  border: 3px solid var(--color);
  border-radius: 5px;

  position: relative;
  overflow: hidden;
}

.c-info .memories .img > img {
  transform: skewX(7deg) scale(1.2);

  width: 100%;
  height: 100%;
  object-fit: cover;

  transition: transform 0.4s;
}

.c-info .memories .img:hover img {
  transform: skewX(7deg) scale(1.23);
}

.c-info .memories .img .overlay {
  position: absolute;
  top: 0px;
  left: 0px;

  background-color: var(--fadedc);
  width: 100%;
  height: 100%;

  background-image: linear-gradient(
    var(--color),
    var(--color) 4px,
    #0000 4px,
    #0000 10px
  );

  background-size: 100% 10px;
  animation: bg-pan 10s infinite linear;

  opacity: 0.21;
}

.c-info .name {
  color: var(--color);
  font-size: 50px;
}

.c-info .desc {
  font-size: 17px;

  width: calc(var(--mwidth) * 0.5);
  white-space: pre-line;
  word-wrap: break-word;
  color: #ccc;
}

.c-info .desc.quote {
  font-size: 25px;
  color: var(--color);

  font-weight: 600;
}

.character-box .radio-box {
  margin-bottom: -50px;
}

.character-box .radio-box .bottom {
  color: var(--iwww);
}

.character-box .radio-box .icon {
  border: 4px dashed var(--iwww);
}

.character-box .card-pop-up {
  position: absolute;
  z-index: 1;

  padding-left: 40px;

  background-color: #000;

  overflow: hidden;

  height: 0px;
  width: calc(50%);

  transition: height 0.1s;

  display: flex;
  align-items: center;
  color: #fff;

  word-break: keep-all;
  white-space: pre-wrap;
}

.character-box .card-pop-up > div {
  white-space: pre-line;
  width: 100%;
}

.character-box .card-pop-up.active {
  height: 100%;
}

@keyframes bg-pan {
  from {
    background-position: 0% 0%;
  }

  to {
    background-position: 0% -100%;
  }
}
