.image-browser {
  color: #fff;

  display: flex;
  align-items: center;
  overflow: hidden;
}

.image-preview-box {
  flex: 1;
  height: var(--mheight);

  background-size: auto 80%;
  background-position: center;
  background-repeat: no-repeat;

  display: inline-block;
}

.image-browser.full .image-preview-box {
  background-size: auto 100%;
}

.image-browser .image-list {
  overflow-y: auto;
  overflow-x: hidden;
  height: var(--mheight);

  transition: width 0.15s;

  z-index: 2;
  position: relative;
  background-color: #000000;
}

.image-browser .image-list .list-item {
  font-size: 16px;
  text-transform: uppercase;
}

.image-list .list-header {
  position: sticky;
  text-align: center;
  top: 0px;

  margin: 0px;
  padding: 0 0 0 10px;

  background-color: #131313;
  z-index: 2;

  border-bottom: 2px solid #ffff;

  white-space: nowrap;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.image-list .main-btt {
  justify-content: flex-start;
  gap: 12px;
  white-space: nowrap;
}

.image-browser .image-list::-webkit-scrollbar {
  width: 10px;
}

.image-browser .image-list::-webkit-scrollbar-thumb {
  background-color: #fff;
}

.image-browser .guide {
  position: absolute;
  bottom: 0px;
  left: 0px;

  width: 100%;
  z-index: 5;

  padding: 10px;
  gap: 30px;

  font-weight: 600;
  transform: translateX(200px);
  opacity: 1;

  transition: transform 0.15s, opacity 0.15s;
}

.image-browser.full .guide {
  transform: translateX(0px);
  opacity: 0.1;
}
