.deck-selector {
  --w: 450px;
}

.deck-selector .preview {
  width: var(--w);
  height: calc(100% - 0px);

  background-color: #131313;

  transform: translateX(var(--w));
  transition: transform 0.1s;

  overflow-y: auto;
  overflow-x: hidden;
}

.deck-selector .list-header {
  position: sticky;
  top: 0px;

  background-color: #131313;

  padding: 20px 0px;
  z-index: 4;

  border-bottom: 1px dashed #fff5;
}

.deck-selector .preview::-webkit-scrollbar {
  width: 3px;
}

.deck-selector .deck-list {
  transform: translateX(calc(var(--w) * 0.5));
  transition: transform 0.1s;

  flex: 1;
  zoom: 0.9;
}

.deck-selector.active .deck-list,
.deck-selector.active .preview {
  transform: translateX(0px);
}

.row-list .item {
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0px 10px 0px 10px;
  gap: 10px;

  --fill-perc: 1px;

  background-image: linear-gradient(
    90deg,
    var(--color),
    var(--color) var(--fill-perc),
    #0000 var(--fill-perc),
    #0000
  );

  background-color: var(--fader);
  font-weight: bold;

  transition: --fill-perc 0.1s;

  border-bottom: 1px solid var(--fader);
  border-top: 1px solid var(--fader);
}

.row-list .item:hover {
  --fill-perc: 6px;
}

.preview .section-sep {
  margin: 20px 20px 0px 20px;
}

.preview .drop-item {
  --l: calc(var(--w) * 0.165);
  width: var(--l);
  height: var(--l);
}

.preview .drop-item .wrapper {
  height: 0px;
  width: 100px;

  transition: none;
}

.preview .drop-item:hover .wrapper {
  height: 45px;
  width: 100px;
}

.preview .drop-item:hover .wrapper .title {
  padding-left: 10px;
}

.preview .drop-item:hover .wrapper:hover {
  width: 0px;
}

.preview .drop-item .wrapper .list-drop {
  display: none;
}

.preview .drop-item {
  filter: none;
}

.transform-top-origin {
  animation: tfto 0.2s ease-out 0.5s 1 normal forwards;
  background-color: #000;
  position: absolute;

  width: var(--mwidth);
  height: var(--mheight);
  z-index: 2;

  transform-origin: top;
}

.deck-item-wrapper {
  width: calc(100% - 10px);
  height: var(--mheight);
  margin: 30px;
  padding: 10px;

  overflow-y: auto;
  overflow-x: hidden;
}

.deck-item-layout {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
  align-items: flex-start;
  row-gap: 20px;
  column-gap: 30px;
}

.deck-item-wrapper::-webkit-scrollbar {
  width: 0px;
}

@keyframes tfto {
  0% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(0);
  }
}
