.input-text {
  font-size: 24px;

  padding: 20px;
  height: 50px;

  margin: 5px 0px;
  font-weight: 600;
}

.input-text .input {
  flex: 1;
  height: 100%;
  background-color: #000;

  display: flex;
  align-items: center;
  justify-content: center;

  gap: 10px;

  border: 2px #fff solid;
  color: #fff;

  padding: 0px 10px;
}

.input-text .input input {
  flex: 1;
  height: calc(100% - 10px);

  background: none;
  border: none;

  font-size: 20px;
  font-weight: inherit;
  font-family: "Oxanium", monospace;
  font-size: 20px;

  outline: none;
  color: inherit;
}
