.context-menu {
  position: absolute;
  top: 0px;
  left: 0px;

  --c: #fff;

  min-width: 300px;
  min-height: 100px;

  border: #fff2 solid;
  border-width: 2px 0px;

  z-index: 10;

  background-color: #131313;

  animation: fade-in-class 0.2s linear 0s 1 normal forwards;
}

.context-menu .option {
  height: 30px;
  padding: 5px 10px 5px 20px;
  color: #fff;
  font-size: 15px;

  display: flex;
  align-items: center;

  border-bottom: 1px solid #fff2;

  font-weight: 700;

  --fill-perc: 5px;

  background-image: linear-gradient(
    90deg,
    var(--c),
    var(--c) var(--fill-perc),
    #0000 var(--fill-perc),
    #0000
  );

  transition: --fill-perc 0.2s, color 0.2s;
}

.context-menu .option .icon {
  width: 50px;
}

.context-menu .option.big-font {
  font-size: 30px;
  padding-bottom: 10px;
}

.context-menu .option.active,
.context-menu .option:hover {
  /* cursor: pointer; */
  --fill-perc: 100%;
  color: #000;
}

.context-menu .option:not(.notice):active {
  filter: brightness(0.8);
}
