.reprogram-tab {
  height: 100%;
  gap: 20px;

  --w: max(200px, min(calc(0.25 * var(--mwidth) - 70px), 700px));
}

.reprogram-tab > *:not(.seperator) {
  --w: 360px;

  overflow: hidden scroll;
  width: calc(var(--w) - 3px);
  height: 100%;

  border: #fff solid;
  border-width: 0 1px 0 1px;
}

.reprogram-tab > *:not(.seperator)::-webkit-scrollbar {
  width: 5px;
  background-color: #fff1;
}

.reprogram-tab > *:not(.seperator)::-webkit-scrollbar-thumb {
  width: 5px;
  background-color: #fff;
}

.reprogram-tab .storage-col,
.reprogram-tab .deck-col,
.reprogram-tab .card-mini-list {
  --w: max(200px, min(calc(0.25 * var(--mwidth) - 70px), 700px));
  position: relative;
}

.reprogram-tab .card-mini-list .search-bar {
  padding: 0px 6px;

  display: flex;
  align-items: center;
  gap: 6px;
}

.card-mini-list.disabled {
  pointer-events: none;
  overflow: hidden;
}

.card-mini-list .disabled-overlay {
  width: 100%;
  height: 100%;

  position: sticky;

  top: 0px;
  left: 0px;

  z-index: 5;
  background-color: #000d;

  display: none;
}

.card-mini-list.disabled .disabled-overlay {
  display: flex;
}

.card-mini-list .search-bar input {
  height: 100%;
  padding: 0px;

  flex: 1;
  border: none;
  outline: none;
  background: none;
  color: #fff;

  font-family: var(--font2);
  font-size: 18px;
}

.card-mini-list .search-bar .zum {
  transition: scale 0.05s;
  /* cursor: pointer; */
}

.card-mini-list .search-bar .zum:active {
  filter: brightness(0.8);
  transform: scale(0.95);
}

.reprogram-tab .card-info {
  flex: 1;
}

.seperator {
  font-size: 60px;
}

.reprogram-tab .sticky {
  height: 50px;

  border-bottom: 1px solid;
  background-color: #000;

  position: sticky;
  top: 0px;

  z-index: 3;
}

.reprogram-tab .card-info.preview .card-preview {
  background-size: 180px, cover, 420px, 100% 466px;
  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;

  background-position: center 70px, center, center -100px, center -100px;
  image-rendering: pixelated;
}

.reprogram-tab .drop-tray {
  width: 60px;
  margin-right: -15px;

  border: none;
  white-space: nowrap;
  overflow: visible;

  z-index: 8;
}

/* .reprogram-tab:not(.min) .drop-tray .drop-item:not(.active) {
  opacity: 0.5;
} */

.reprogram-tab .drop-tray .drop-item {
  width: 100%;
}

.reprogram-tab .drop-tray:hover .drop-item {
  opacity: 1;
}

.reprogram-tab.min .storage-col,
.reprogram-tab.min .deck-col,
.reprogram-tab.min .card-mini-list {
  --w: max(200px, min(calc(0.3 * var(--mwidth) - 70px), 700px));
}

@keyframes size-up {
  0% {
    background-size: calc(100% - 10px);
  }
  50% {
    background-size: calc(100%);
  }
  100% {
    background-size: calc(100% - 10px);
  }
}

@keyframes size-down {
  0% {
    background-size: calc(100% - 10px);
  }
  50% {
    background-size: calc(100% - 20px);
  }
  100% {
    background-size: calc(100% - 10px);
  }
}
