.drop-item {
  width: 60px;
  height: 60px;

  background-position: center;
  background-size: calc(100% - 10px);
  background-repeat: no-repeat;

  transition: filter 0.2s;
  position: relative;

  animation: size-down 0.2s ease-out 0s 1 normal forwards;
  display: inline-block;
}

.drop-item:hover {
  background-color: #ffff;
}

.drop-item.active {
  filter: none;
  animation: size-up 0.2s ease-out 0s 1 normal forwards;
  /* filter: drop-shadow(-2px 0px 0px #fff); */
}

.drop-item {
  filter: grayscale(1);
}

.drop-item.active {
  filter: none;
}

.drop-item .wrapper {
  --exp: 110px;
  --d: 0.2s;

  position: absolute;
  z-index: 3;

  left: calc(100% + 6px);
  top: 0px;

  width: 0px;
  height: 100%;
  /* padding-left: 20px; */

  background-color: #000;
  overflow: hidden;

  border: solid #fff0;
  border-width: 0px;

  transition: width var(--d), height 0.3s calc(var(--d) + 0.1s),
    border-color 0.3s;
}

.drop-item .wrapper .main-btt {
  font-size: 16px;
}

.drop-item:hover .wrapper {
  border-color: var(--orange);
  width: calc(var(--w) + 20px - 10px);
  height: 110px;
  border-width: 1px;

  white-space: normal;
}

.drop-item .wrapper:hover {
  height: var(--exp);
  transition: height 0.1s, opacity 50ms;
}

.drop-item:hover .wrapper .title {
  border-bottom: 1px dashed #fff3;
  margin-bottom: 5px;
}

.drop-item .wrapper .list-drop,
.drop-item .wrapper .title {
  padding-left: 20px;
  padding-right: 10px;
  text-align: left;
}
