.main-btt {
  padding: 10px 30px;
  min-width: 100px;

  --bg-color: #0000;
  --hv-bg-color: #fff;

  --tx-color: #fff;
  --hv-tx-color: #000;

  border-left: 3px solid var(--hv-bg-color);
  color: var(--tx-color);

  --fill-perc: 3px;

  font-size: 24px;
  font-weight: 700;

  background-image: linear-gradient(
    90deg,
    var(--hv-bg-color),
    var(--hv-bg-color) var(--fill-perc),
    var(--bg-color) var(--fill-perc),
    var(--bg-color)
  );

  transition: --fill-perc 0.2s, color 0.4s;
}

.main-btt.active,
.main-btt:hover {
  color: var(--hv-tx-color);
  --fill-perc: 100%;
}
