.card-row {
  height: 60px;

  border: solid #fff1;
  border-width: 1px 0px;
  margin-bottom: 1px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  transition: all 0.1s;

  background-size: auto, calc(100% - 50px);
  background-position: center;

  position: relative;
  padding: 6px 0px;
}

.card-row:hover {
  border-color: var(--c);
}

/* .card-row > .info .name {
  z-index: 1;
} */

.card-row .icon-wrapper {
  width: 60px;
  height: 60px;

  display: flex;
  align-items: center;
  justify-content: center;

  border-right: 1px solid #fff2;
  pointer-events: none;
}

.card-row .icon-wrapper img.icon {
  width: 50px;
  height: 50px;
}

.card-row .stats .block {
  width: 30px;
  height: 30px;

  display: flex;
  align-items: center;
  justify-content: center;

  font-weight: 900;
}

.card-row .stats .block.y {
  border-left: 1px solid var(--yellow);
  /* color: #000; */
}

.card-row .stats .block.r {
  border-left: 1px solid var(--red);
  color: #fff;
}

.card-row .info {
  flex: 1;
  padding-left: 5px;

  display: flex;
  flex-direction: column;
  gap: 5px;
}

/* .card-row:active .info {
  transition: color 0.2s;
  color: var(--t);
} */

.card-row .info .name {
  font-weight: 700;
  font-size: 15px;
}

.card-row .effects {
  display: flex;
  align-items: center;

  gap: 5px;
}

.card-row .effects .icon {
  width: 25px;
  aspect-ratio: 1;

  border: 1px solid #fff;

  background-size: contain;
  background-color: #000;

  box-shadow: var(--r-shadow) 0px 5px 15px;
}

.card-row .overlay {
  position: absolute;
  top: 0px;
  left: 0px;

  height: 100%;

  background-color: var(--bg);
  font-weight: bold;

  overflow: hidden;
}

.card-row.flash .overlay {
  animation: flashed 0.3s linear 0s 1 normal backwards;
}

.card-row .overlay > .inf {
  margin-left: 65px;
}

.card-row .upgrade .ico {
  /* cursor: pointer; */
  opacity: 0;
  transition: transform 0.2s, opacity 0.1s;
}

.card-row:hover .upgrade .ico {
  opacity: 1;
}

.card-row .upgrade .ico:active {
  filter: brightness(0.7);
}

.card-row .upgrade .ico.down {
  transform: rotate(180deg);
}

.card-row .overlay > .ico {
  margin-right: 5px;
  font-size: 40px;
}

/* .card-row:active .overlay {
  transform: scaleX(1);
} */

@keyframes flashed {
  0% {
    background-color: #fff0;
    color: #fff0;
    width: 100%;
  }
  10% {
    background-color: #fff;
    color: #fff0;
    width: 100%;
  }
  15% {
    background-color: #fff;
    color: #fff0;
    width: 100%;
  }
  to {
    background-color: #fff0;
    color: #fff0;
    width: 100%;
  }
}
