.catalog .search-bar {
  width: calc(100% - 65px);
  padding: 13px 30px;
  height: 35px;

  border-bottom: 1px solid #fff4;

  position: sticky;
  left: 0px;
  top: 0px;

  z-index: 10;
  background: linear-gradient(
    90deg,
    #000 calc(100% - 0px),
    #fff0 calc(100% - 0px)
  );

  color: #fff;
  font-size: 20px;

  gap: 10px;

  /* display: none; */
}

.catalog .search-bar input {
  height: 100%;
  flex: 1;

  color: #fff;
  font-size: inherit;

  font-family: var(--font1);
  background: none;
  border: none;
  outline: none;

  flex: 1;
}

.search-bar .search-filter-panel {
  width: 100%;
  height: 0px;

  background-color: #000;
  position: absolute;
  top: 100%;
  left: 0px;

  border-top: 2px solid #fff;
  border-bottom: 3px solid #fff;

  overflow: hidden;
  pointer-events: none;
  animation: fold-in 0.1s;
}

.search-bar .search-filter-panel.active {
  pointer-events: initial;
  animation: flash-fold-out 0.2s linear 0s 1 normal forwards;
}

.catalog {
  /* margin-top: 100px; */
  height: calc(var(--mheight) - 70px);

  border-top: 3px solid #fff;
}

.catalog .sidebar {
  width: 350px;
  height: calc(100% - 100px);
  border-right: 4px solid #fff;
  overflow: auto;

  padding-bottom: 100px;
}

.catalog .fader {
  position: absolute;
  bottom: 40px;
  left: 0px;

  width: calc(100% - 40px);
  height: 0px;
  padding: 20px;

  justify-content: flex-end;
  overflow: visible;
  z-index: 5;
}

.catalog .sidebar .option {
  border-top: 2px solid #fff3;
  /* border-bottom: 2px solid #fff; */

  height: 60px;
  font-size: 13px;

  padding: 0px 20px;

  display: flex;
  align-items: center;

  --c: #fff;

  color: #fff;

  gap: 15px;

  --fill-perc: 0px;

  background-image: linear-gradient(
    90deg,
    var(--c),
    var(--c) var(--fill-perc),
    #0000 var(--fill-perc),
    #0000
  );

  transition: --fill-perc 0.1s;
  background-color: #000;
}

.catalog .sidebar .option:hover {
  background-color: #fff1;
  --fill-perc: 10px;
}

.catalog .sidebar .option.all {
  z-index: 2;
  position: sticky;
  top: 0px;
  margin-top: -100px;
}

.catalog .sidebar .option.active,
.catalog .sidebar .option:active {
  z-index: 2;

  --fill-perc: 100%;
  position: sticky;
  top: -1px;
}

.catalog .sidebar .option.active {
  animation: button-go-side-and-back 0.1s linear 0s 1 normal forwards;
}

.catalog .sidebar .option.filter {
  height: 50px;
  position: sticky;
  top: 61px;
  z-index: 5;

  --fill-perc: 0%;
  border: none;
  background-color: #000;

  padding: 0 10px 0 10px;
}

.catalog .sidebar .blank {
  position: sticky;
  top: 0px;
  z-index: 2;

  background-color: #000;

  height: 100px;
}

.catalog .sidebar .option .icon {
  height: 50px;
}

.catalog .sidebar .option .icon.blend,
.catalog .sidebar .info {
  mix-blend-mode: difference;
}

.catalog .sidebar .option .title {
  font-weight: 700;
  font-size: 20px;

  text-transform: capitalize;
}

.catalog .sidebar .option .txt-ico {
  padding: 0 16px;
  font-size: 16px;
}

.catalog .sidebar .option.active .txt-ico,
.catalog .sidebar .option:active .txt-ico {
  color: #000;
}

.catalog .card-wrapper {
  height: 100%;
  width: calc(100% - 700px);

  overflow: hidden scroll;
  flex: 1;
}

.whopper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));

  row-gap: 10px;
  column-gap: 30px;

  padding: 30px 2 0px 100px 30px;

  padding: 10px;
}

.whopper .card-item {
  width: auto;
  font-size: 15px;

  margin-bottom: 50px;
  margin-right: 20px;

  max-width: 180px;
  animation: card-snap-down 0.2s linear 0s 1 normal forwards;
}

.catalog .card-wrapper::-webkit-scrollbar {
  width: 10px;
}

.catalog .sidebar::-webkit-scrollbar {
  width: 0px;
}

.catalog .card-wrapper::-webkit-scrollbar-thumb {
  background-color: #000a;
}

.catalog .card-wrapper::-webkit-scrollbar-thumb:hover {
  background-color: #0005;
}

.catalog .card-wrapper::-webkit-scrollbar-track {
  background-color: #fff;
}

.catalog .card-preview {
  width: 450px;
  height: 100%;

  padding: 0px 10px;

  /* display: none; */

  background-size: 180px, cover, 420px, 100% 466px;
  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;

  background-position: center 120px, center, center -50px, center -50px;
  image-rendering: pixelated;
}

@keyframes flash-fold-out {
  0% {
    height: 0px;
    background-color: #000;
  }
  40% {
    background-color: #bbb;
    height: 60px;
  }
  60% {
    background-color: #bbb;
  }
  100% {
    background-color: #000;
    height: 60px;
  }
}

@keyframes fold-in {
  from {
    height: 60px;
  }
  to {
    height: 0px;
  }
}

@keyframes fly-in {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0%);
  }
}

@keyframes card-snap-down {
  0% {
    margin-top: -15px;
    opacity: 0;
  }
  80% {
    opacity: 0.6;
  }
  100% {
    margin-top: initial;
    opacity: 1;
  }
}

@keyframes button-go-side-and-back {
  0% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0px);
  }
}
