.card-item {
  width: 350px;
  aspect-ratio: 1 / 1.265;

  background-size: 65%, cover;
  background-position: center;
  background-repeat: no-repeat;

  box-shadow: var(--r-shadow) 0px 5px 0px, var(--r-shadow) 0px 0px 10px;
  border: 1px solid var(--r-shadow);

  /* border-width: 0; */

  display: inline-block;
  position: relative;

  color: #fff;
  font-size: 31px;

  transition: box-shadow 90ms, transform 90ms;
}

.card-item.border:not(:hover) .effects .icon,
.card-item.border:not(:hover) {
  box-shadow: none;
}

.card-item:hover {
  transform: translateY(-5px);
}

.card-item:active {
  transform: translateY(0px);
  box-shadow: var(--r-shadow) 0px 0px 10px;
}

.card-item .title {
  text-align: center;
  height: 15%;

  font-size: 1em;
  padding: 20px 0px;

  font-weight: 600;

  color: var(--r-text);
}

.card-item .underline.thin {
  border-bottom: 2px solid var(--r-text);
  display: inline-block;
  padding: 0 5px;
}

.card-item .image {
  height: calc(35% + 10px);
}

.card-item .stats {
  font-size: 2em;
  padding: 0px 15px;

  transform: translateY(-50%);
}

.card-item .effects {
  position: absolute;
  width: 100%;
  bottom: 0px;
}

.card-item-level,
.card-item .level {
  --r: var(--r-text);
  height: 10px;
  margin-bottom: -10px;
}

.card-item-level .bl,
.card-item .level .bl {
  width: 10px;
  height: 10px;

  margin: 3px;
  display: inline-block;

  background-color: var(--r);
  box-shadow: 2px 2px 4px #666;
}

.card-item.no-effects .effects {
  display: none;
}

.card-item .effects .icon {
  width: calc(30% - 15px);
  aspect-ratio: 1;

  border: 2px solid var(--r-text);

  background-size: contain;
  background-color: #000;
  background-repeat: none;

  transform: translateY(50%);

  box-shadow: var(--r-shadow) 0px 5px 15px;
  overflow: visible;

  gap: 5px;
}

.card-item .effects .icon .subicon {
  width: calc(50% - 3px);
  height: calc(50% - 3px);

  transform: translateY(calc(100% + 15px));
  background-size: contain;
  background-repeat: none;

  border: 1px dashed var(--r-text);
}

div:not(.pop-panel) .card-item .view-upgrade {
  position: absolute;
  right: -35px;
  top: 0px;

  width: 25px;
  height: 25px;

  border: var(--r-text) dashed 2px;
}
