.float-bg-img {
  height: calc(var(--mheight) * 1);
  width: calc(var(--mwidth) * 3);

  transform-origin: top left;
  /* transform: scale(0.6) rotate(-10deg) translate(-400px, 80px); */

  animation: scrolling 150s linear 0s infinite alternate forwards;

  position: absolute;
  top: 0px;
  left: 0px;

  z-index: 0;

  /* filter: blur(3px); */
}

.float-bg-img .card-item {
  margin: 50px;
}

@keyframes scrolling {
  0% {
    transform: scale(0.7) rotateX(45deg) rotateY(0deg) rotateZ(340deg)
      translate(-649px, 0px);
  }
  100% {
    transform: scale(0.7) rotateX(45deg) rotateY(0deg) rotateZ(340deg)
      translate(-2649px, 0px);
  }
}
