.main-panel.deck-editor {
  display: flex;
  align-items: center;
  justify-content: center;

  flex-direction: column;
}

.deck-editor .splitter {
  width: 100%;
  height: 2px;

  --sf: 0%;
  --ef: 1%;

  background: linear-gradient(
    90deg,
    #fff7 calc(var(--sf) - 50px),
    #fffe var(--sf),
    #fffe var(--ef),
    #fff7 calc(var(--sf) + 50px),
    #fff1
  );
}

.deck-editor .tab-header {
  width: 100%;
  padding-top: 20px;
}

.tab-header .KlidZhblvp {
  --zums: 1.003;
  --width: auto;

  height: 45px;
  width: var(--width);
  margin: 0 2px 0 0;

  border-radius: 5px 5px 0 0;
  border: solid #fff;
  /* border-width: 3px 3px 0px 1px; */
  border-width: 0px;

  background-color: #fff4;

  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 10px;
  vertical-align: bottom;

  font-weight: 700;
  font-size: 16px;

  overflow: hidden;
  transition: width 0.3s;

  animation: width-increase 80ms ease-out 0s 1 normal forwards;
  transition: background 0.1s, color 0.1s;
}

.tab-header .KlidZhblvp.small {
  background-color: #ffffffcc;
  color: #000000;

  height: 40px;
  width: auto;
  margin: 0 0 0 -2px;

  border-radius: 0px 5px 0px 0px;
  animation: none;
}

.tab-header .KlidZhblvp.catalog-def {
  --width: 334px;
}

.tab-header .KlidZhblvp:hover {
  background-color: #fff7;
}

.tab-header .KlidZhblvp:active {
  background-color: #fff5;
}

.tab-header .KlidZhblvp.active {
  background-color: #ffffff;
  color: #000000;
}

.deck-editor .content-container {
  width: 100%;
  flex: 1;
}

@keyframes width-increase {
  0% {
    width: 20px;
  }
  100% {
    width: var(--width);
  }
}
