.help-item {
  --s: 40px;
  width: var(--s);
  height: var(--s);
  border-radius: var(--s);

  background-color: #fff;
  color: #000;
}

.help-item .tips {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 200;

  color: #fff;

  height: auto;
  width: 0px;

  transition: border-width 0.2s, width 0.2s;

  border: solid #fff;
  border-width: 0px;
  padding-bottom: 20px;

  overflow: hidden;
  white-space: pre;

  background-image: linear-gradient(151deg, #0a0a0a, #131313);
}

.help-item:hover .tips {
  width: 750px;
  border-width: 0px 2px;

  padding-left: 10px;
  padding-right: 30px;
}

.help-item .tips > h4 {
  margin: 10px 0 10px 0;
  font-size: 25px;

  border-bottom: 2px solid #fffa;
  color: var(--yellow);
}

.help-item .tips > div {
  font-size: 17px;
  font-weight: 500;
  text-transform: none;
}
