.radio-box {
  position: relative;
  width: 190px;
  height: auto;
  --color: #fff;

  color: #000;
  font-size: 20px;
  font-weight: 900;
  text-transform: uppercase;

  transition: transform 0.1s;

  display: inline-flex;
  flex-direction: column;
  gap: 10px;

  vertical-align: top;
}

.radio-box .bottom {
  width: 100%;
  text-align: center;
  color: #fff;

  transition: transform 0.2s;
}

.radio-box .bottom .title {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  word-wrap: break-word;
  -webkit-box-orient: vertical;
}

.radio-box .icon {
  --fill-perc: 0px;

  background-image: linear-gradient(
    0deg,
    var(--color),
    var(--color) var(--fill-perc),
    #000 var(--fill-perc),
    #000
  );

  color: #fff;
  width: calc(100% - 40px);
  border: 2px solid var(--color);

  aspect-ratio: 1;
  transition: --fill-perc 0.1s;
}

.radio-box.active .icon {
  --fill-perc: 100%;
}

.radio-box.active,
.radio-box:hover {
  transform: scale(1.02);
}

.radio-box:active {
  transform: scale(0.982);
}

.radio-box.animated .bottom {
  transition: transform 0.2s, opacity 0.15s;
  transform: translateY(-100%);
  opacity: 0;
}

.radio-box.animated:hover .bottom {
  transform: translateY(0px);
  opacity: 1;
}
