.credit-sec {
  padding: 50px;
  height: calc(100% - 100px);
  overflow: auto;
  color: #fff;
}

.credit-sec::-webkit-scrollbar {
  width: 0px;
}

.role-credit {
  font-size: 30px;
  text-align: center;
  line-height: 1.5;

  margin: 10vh 0 50px 0;
}

.role-credit .role {
  color: var(--yellow);
  font-weight: 700;
  text-transform: uppercase;
}

.role-credit .name {
  color: #fff;
  font-weight: 700;
  font-size: 1.25em;
}

.credit-sec .return-button {
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.credit-sec .crtitle {
  font-size: 100px;
  text-align: center;

  border-bottom: 1px solid #fff4;
}

.credit-sec .crsubtitle {
  font-size: 40px;
  text-align: center;

  margin-top: 100px;
  text-transform: uppercase;
}

.errtitle {
  font-size: 130px;
  color: var(--red);

  animation: splash 0.8s linear 0s infinite normal forwards;
  line-height: 1;
}

.errsub {
  color: var(--yellow);
  text-transform: uppercase;
}

.long-boi {
  white-space: nowrap;
  overflow: visible;

  font-size: 10vh;
  width: fit-content;
}

.para {
  width: calc(100% - 400px);
  padding: 200px;

  white-space: pre-line;
  font-size: 60px;
  text-align: center;
  font-weight: 700;

  line-height: 3;
  color: #ff0;

  position: relative;
  transform-origin: 50% 100%;

  transform: rotateX(65deg) translateY(calc(-120% - 50vh)) scaleY(1.1);

  overflow: hidden;
  height: fit-content;

  text-shadow: 0px 6px 1px #ff09;
}

@keyframes splash {
  0% {
    opacity: 1;
  }
  40% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
