.catalog-tab .side {
  width: 350px;
  height: 100%;

  overflow-y: scroll;
  overflow-x: hidden;
}

.catalog-tab .side::-webkit-scrollbar {
  width: 0px;
}

.catalog-tab .preview {
  min-height: 100%;
}

.catalog-tab .card-wrapper {
  min-height: 100%;
  zoom: 0.9;
}

.catalog-tab.catalog {
  border: none;
}

.catalog-tab .sidebar {
  padding-bottom: 0px;
  height: 100%;
}
