.input-button {
  --width: 200px;

  width: var(--width);
  height: 60px;

  padding: 8px 30px;

  font-size: 18px;
  font-weight: 700;

  --size: 10px;
  --c: #eeff00;
  --bg-color: #fff2;

  background-image: linear-gradient(
    90deg,
    var(--c),
    var(--c) var(--size),
    #0000 var(--size),
    #0000
  );

  background-color: var(--bg-color);
  /* cursor: pointer; */

  transition: --size 0.2s, color 0.2s;
  font-size: 25px;

  color: #fff;
}

.input-button:not(.disabled).active,
.input-button:not(.disabled):hover {
  --size: calc(var(--width) + 200px);
  color: var(--hover-color);
}

.input-button.disabled {
  color: var(--hover-color);

  filter: brightness(0.7);
  /* cursor: default; */

  --size: 10px;
}

.input-button.zum.disabled {
  filter: none;
  transform: none;
}

@keyframes lul {
  0% {
    --size: calc(var(--width) + 200px);
  }
  100% {
    --size: calc(var(--width) + 200px);
  }
}
