.pop-panel-container {
  position: absolute;

  top: 0px;
  left: 0px;

  width: var(--mwidth);
  height: 0px;

  z-index: 100;

  display: flex;
  align-items: center;
  justify-content: center;

  --t: 0.2s;
  transition: height 0s var(--t);
  overflow: hidden;

  backdrop-filter: blur(10px);
  background-color: #0006;
}

.pop-panel-container .pop-panel {
  position: relative;
}

.pop-panel-container.active {
  transition: height 0s;

  height: var(--mheight);
}

.pop-panel-container .pop-panel {
  border-top: 2px solid #fff;
  border-bottom: 2px solid #fff;
  background-color: #0002;

  overflow: hidden;

  animation: var(--t) ease-out popPanelGoOut;
  animation-fill-mode: forwards;
}

.pop-panel-container.active .pop-panel {
  animation: 0.2s ease-out popPanelGoIn;
  animation-fill-mode: forwards;
}

.pop-panel-container .pop-panel .content .center {
  text-align: center;
}

.pop-panel-container .pop-panel .content .expand-full-panel {
  position: absolute;
  top: 0px;
  left: 0px;

  z-index: 0;
  height: 100%;
  width: 100%;
}

.pop-panel > .title {
  width: 100%;

  font-size: 30px;
  font-weight: bold;
  color: #ffff00;
  margin-bottom: 10px;

  z-index: 2;
  position: relative;

  text-align: center;
}

@keyframes popPanelGoIn {
  0% {
    width: 10px;
    height: 10px;
  }
  50% {
    width: 100%;
    height: 10px;
  }
  60% {
    background-color: #777;
  }
  100% {
    height: auto;
    width: 100%;
    background-color: #000;
    padding: 20px 100px;
  }
}

@keyframes popPanelGoOut {
  0% {
    max-height: var(--mheight);
    width: 100%;
    background-color: #000;
  }
  70% {
    width: 100%;
    max-height: 5px;
  }
  100% {
    width: 0%;
    max-height: 5px;
  }
}
