.bottom-pop-up-wrapper {
  --p-color: #ffffff;
  --s-color: #ffffff22;

  position: absolute;
  bottom: 100px;
  left: 0px;

  width: var(--mwidth);
  height: 80px;

  transform: translateY(var(--mheight));
  transition: transform 0.1s 0.2s;
  z-index: 10;
}

.bottom-pop-up-wrapper.active {
  transition: transform 0.1s;
  transform: translateY(0px);
}

.bottom-pop-up {
  --max-width: 500px;

  width: 0px;
  height: 10px;

  background-image: linear-gradient(
    90deg,
    var(--s-color),
    #fff1,
    var(--s-color)
  );

  border-left: 2px solid #fff;
  border-right: 2px solid #fff;

  color: #fff;
  font-size: 20px;
  font-weight: 600;

  animation-name: bottomPopPanelGoOut;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;

  overflow: hidden;
  gap: 10px;

  backdrop-filter: blur(10px);
}

.bottom-pop-up .title {
  color: var(--p-color);
  font-weight: bold;
  text-transform: uppercase;
}

.bottom-pop-up .content {
  color: #fff;
  font-weight: 500;
  font-size: 15px;
}

.bottom-pop-up-wrapper.active .bottom-pop-up {
  animation-name: bottomPopPanelGoIn;
  animation-duration: 0.3s;
  animation-delay: 0.1s;
  animation-fill-mode: forwards;
}

@keyframes bottomPopPanelGoIn {
  0% {
    width: 0px;
    height: 10px;
  }
  10% {
    background-color: #fff;
  }
  50% {
    width: 0px;
    height: 100%;
  }
  80% {
    background-color: #fff;
  }
  100% {
    width: var(--max-width);
    height: 100%;
    background-color: #fff1;
    padding: 6px 10px;
  }
}

@keyframes bottomPopPanelGoOut {
  0% {
    width: var(--max-width);
    height: 100%;
    background-color: #fff1;
    padding: 6px 10px;
  }
  50% {
    width: 0px;
    height: 100%;
  }
  80% {
    width: 0px;
    height: 10px;
  }
  100% {
    width: 0px;
    height: 10px;
  }
}
