@import url("https://fonts.googleapis.com/css2?family=Oxanium:wght@200;300;400;500;600;700;800&family=Play:wght@400;700&display=swap");

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #fff;
}

::-webkit-scrollbar-track {
  background-color: #fff2;
}

html {
  height: 100%;
  padding: 0;
  margin: 0;
  color: #fff;

  user-select: none;
  cursor: none !important;
}

body {
  --zoom: 1;
  --font2: "Oxanium", cursive;
  --font1: "Play", sans-serif;

  font-family: var(--font2);
  background-color: #000000;

  --mwidth: 100vw;
  --mheight: 100vh;
  --zoom: 1;

  --white: #ffffff;
  --green: #00ff00;
  --blue: #0089ff;
  --red: #b10000;
  --yellow: #ffff45;
  --purple: #9f00a7;
  --pink: #ff008a;

  padding: 0px;
  margin: 0px;
  overflow: hidden;

  color: #fff;

  zoom: var(--zoom);

  cursor: url("data:image/png;base64,")
      0 0,
    auto;
}

#app-custom-cursor {
  display: none;
  position: absolute;
  top: 0px;
  left: 0px;

  width: 25px;
  height: 25px;

  background-image: url("../public/cursor.png");
  background-size: cover;

  transform-origin: bottom right;
  z-index: 100;
}

.flex {
  display: flex;
}

.iflex {
  display: inline-flex;
}

.flexing {
  display: flex;
  flex: 1;
}

.aictr {
  align-items: center;
}

.jcctr {
  justify-content: center;
}

.coll {
  flex-direction: column;
}

.spard {
  justify-content: space-around;
}

.spbtw {
  justify-content: space-between;
}

.spevl {
  justify-content: space-evenly;
}

.spend {
  justify-content: flex-end;
}

.flend {
  align-items: flex-end;
}

.flart {
  align-items: flex-start;
}

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ellipsis.two {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.a-like:hover {
  text-decoration: underline;
}

.g5 {
  gap: 5px;
}

.cap {
  text-transform: capitalize;
}

.g10 {
  gap: 10px;
}

.underline {
  --c: #fff;
  border-bottom: 8px solid;
  border-color: var(--c);

  text-align: center;
}

.underline.fixed {
  min-width: 50px;
}

.zum:active {
  transform: brightness(0.49);
}

.y {
  --c: #e0d506;
}

.b {
  --c: #97dfff;
}

.g {
  --c: var(--green);
}

.r {
  --c: #b10000;
}

.p {
  --c: #ce339f;
}

.buffed::after,
.buffed::before {
  font-size: 40px;
  font-weight: bold;
  content: "[";
  color: #97dfff;
}

.buffed::after {
  content: "]";
}

.main-panel,
.exp-panel {
  width: var(--mwidth);
  height: var(--mheight);

  color: #fff;
}

.main-panel {
  background-color: #060606;
}

.main-panel > .image {
  width: calc(var(--mwidth) * 0.65 - 40px);
  height: var(--mheight);

  background-size: 90%;
  background-position: center;
  background-repeat: no-repeat;
}

.main-panel .menu {
  --a: 0px;

  width: calc(var(--mwidth) * 0.35);
  height: var(--mheight);

  transition: margin 0.2s;
}

.logo-wrapper {
  /* border: 3px solid #fff; */
  z-index: 1;

  padding-bottom: 60px;

  /* background-color: #fff; */

  text-shadow: 10px 3px 4px #000a;
}

.logo-wrapper .logo {
  width: 90%;
  z-index: 1;

  filter: drop-shadow(10px 3px 4px #000a);
}

.logo-wrapper .logo-text {
  width: 90%;
  z-index: 1;

  font-size: 35px;
  padding-left: 10%;
  font-weight: 600;

  margin-top: -100px;
  color: #fff;
}

.main-panel .menu .options {
  gap: 40px;
  width: 100%;
}

.main-panel .menu .options .option {
  width: calc(100% - 50px);
  height: 70px;

  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 35px;
  font-weight: 700;

  /* margin-to: 20px 0; */
  background-color: #000;

  border-left: 5px solid var(--c);
  border-right: 5px solid var(--c);

  --fill-perc: 0px;

  background-image: linear-gradient(
    180deg,
    var(--c),
    var(--c) var(--fill-perc),
    #0000 var(--fill-perc),
    #0000
  );

  color: var(--c);

  transition: --fill-perc 0.1s, color 0.2s;

  /* cursor: pointer; */
  user-select: none;
  text-transform: uppercase;
}

.main-panel .menu .options .option.active,
.main-panel .menu .options .option:hover {
  --fill-perc: 100%;
  color: #000;

  z-index: 3;
}

.zum {
  transform: scale(1);
  transition: all 0.1s;
  /* cursor: pointer; */
}

.zum:active {
  transform: scale(0.95);
  filter: brightness(0.8);
}

.tc {
  text-align: center;
}

.filter-bgg {
  /* padding: 20px; */

  --fill-perc: 0px;
  --fill-amnt: 0px;

  width: calc(100%);
  height: calc(100%);

  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 0;

  background-image: linear-gradient(
    0deg,
    #000,
    #000 var(--fill-amnt),
    var(--c) var(--fill-amnt),
    var(--c) var(--fill-perc),
    #0000 var(--fill-perc),
    #0000
  );

  transition: --fill-perc 0.3s, --fill-amnt 0.3s, color 0.2s 0.1s;

  color: #000;
  background-color: #0005;
}

.main-panel.a-1 .filter-bgg * {
  opacity: 0;
}

.main-panel:not(.a-1) .filter-bgg {
  --fill-perc: 100%;
  z-index: 2;
  color: #000;
}

.main-panel.loaded .filter-bgg {
  --fill-amnt: calc(100% - 0px);
}

.main-panel:not(.a-1) .menu {
  pointer-events: none;
}

.main-panel:not(.a-1) .options .option {
  border: 3px solid #000;
}

.main-panel:not(.a-1).loaded .options .option {
  width: var(--mwidth);
  --c: #0000;
  background: none;
  color: #fff0;

  border: none;
}

.main-panel:not(.a-1) .menu {
  margin-top: calc(-230px * var(--i) - var(--a));
}

.main-panel.loaded .menu {
  --a: 705px;
  /* color: #0000; */
}

/* .main-panel .filter-bgg .exp-panel {
  animation: exp-fly-in 0.3s linear 1 normal forwards;
} */

.filter-bgg .loading {
  width: calc(var(--mwidth) * 0.35 - 50px);
  font-weight: 700;
  font-size: 20px;
}

/* */

.sharp {
  image-rendering: auto;
}

.fade-in {
  animation: fade-in-class 0.15s linear 0s 1 normal forwards;
}

/* */

.bottom-info-bar {
  width: var(--mwidth);

  position: absolute;
  bottom: 0px;
  left: 0px;

  padding: 10px;
  font-weight: bold;
  font-style: italic;

  font-size: 14px;
  z-index: 1;

  opacity: 0.3;
  pointer-events: none;
}

/* */

@keyframes fade-in-class {
  0% {
    filter: brightness(0);
    opacity: 0.2;
  }
  66% {
    opacity: 1;
  }
  100% {
    filter: brightness(1);
    opacity: 1;
  }
}

@keyframes zoom-in-out {
  0% {
    transform: scale(1) rotate(0);
  }
  50% {
    transform: scale(1.5) rotate(180deg);
  }
  100% {
    transform: scale(1) rotate(360deg);
  }
}

@keyframes zoom {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes move {
  0% {
    transform: translate(0%, 100%);
  }
  25% {
    transform: translate(-100%, 100%);
  }
  50% {
    transform: translate(-100%, 0%);
  }
  75% {
    transform: translate(0%, 0%);
  }
  100% {
    transform: translate(0%, 100%);
  }
}

@keyframes fly-in {
  0% {
    transform: translateY(100px) skew(7deg);
    opacity: 0;
  }
  100% {
    transform: translateY(0px) skew(7deg);
    opacity: 1;
  }
}

@property --fill-perc {
  syntax: "<length-percentage>";
  inherits: false;
  initial-value: 0px;
}

@property --fill-amnt {
  syntax: "<length-percentage>";
  inherits: false;
  initial-value: 0px;
}

@property --shadow-offset {
  syntax: "<length-percentage>";
  inherits: false;
  initial-value: 0px;
}

@property --c {
  syntax: "<color>";
  inherits: false;
  initial-value: #0000;
}
